import { Injectable } from "@angular/core";
import { IAGSystemInfo } from "@TriInterfaces/mas/mas.avant-guard.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

@Injectable()
export class TriSystemInfoApi extends TriBaseApi<any>{

    constructor(private api: HttpService) {
        super();
    }

    getCsAvantGuardInfo(id: number): Promise<IAGSystemInfo> {
        return this.api.get(`Nuvol9Api/CsAvantGuard/?id=${id}`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value);
        });
    }

    zoneVerification(dataString: any, masAccountId: number): Promise<any> {
        let payload = { "jsonInput": dataString };
        return this.api.post(`Nuvol9Api/MasAccountZoneAssignment/${masAccountId}/ZoneVerification`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value);
        })
    }

    getAvantCsGuardSignalHistory(dataString: any): Promise<any> {
        let payload = JSON.parse(dataString);
        return this.api.post(`Nuvol9Api/CentralStation/AGSignalHistory`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value);
        })
    }
}