import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { IRptFullfilmentInvoiceHeaderModel } from "@TriInterfaces/rpt/tri-IRptFullfilement.interfaces";
import { map } from "rxjs/operators";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";

@Injectable()
export class TriInvoiceApi extends TriBaseApi<any> {
    private readonly apiController = 'Nuvol9Api/CustomerOrders/InvoiceHeader';

    constructor(private api: HttpService) {
        super();
    }

    getInvoiceHeader(id: number): Promise<any> {
        console.log("calling getInvoiceHeader")
        return this.api.get(`${this.apiController}?id=${id}`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value)
        })
    }


    getInvoiceMetaData(): Promise<any> {
        return this.api.get(`Nuvol9Api/Invoice/MetaData`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value)
        })
    }
}