import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { TriSchRootApi } from "app/@core/backend/common/api/sch/schRoot.api";
import { GetOrgScheduleAction } from "./schedule.actions";

export interface TriOrgScheduleStateModel {
    orgSchedule: any
}

const getInitialModel = (): TriOrgScheduleStateModel => ({
    orgSchedule: null
});

@Injectable()
@State<TriOrgScheduleStateModel>({
    name: 'TriOrgScheduleRoot',
    defaults: getInitialModel(),
})
export class TriOrgScheduleState {
    constructor(private triSchRootApi: TriSchRootApi) { }

    @Selector()
    static getOrgScheduleData({ orgSchedule }: TriOrgScheduleStateModel) {
        return orgSchedule;
    }

    @Action(GetOrgScheduleAction)
    getDefaultLeadCrePullModel(
        ctx: StateContext<TriOrgScheduleStateModel>,
        action: GetOrgScheduleAction
    ) {
        const payload: any = action.payload;
       
        this.triSchRootApi.getOrgSchedule(payload).then((response) => {
            const state = ctx.getState();
            let resultant = ctx.patchState({
                ...state,
                orgSchedule: response
            });
            return resultant;
        })
    }
}