import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";

import { TriMetaDataInterface } from "@TriInterfaces/gui/gui.metadata.interfaces";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";

@Injectable()
export class TriGuiMetaDataApi extends TriBaseApi<TriMetaDataInterface> {
    private readonly apiController: string = 'Nuvol9Api/rmd1';

    constructor(private api: HttpService) {
        super();
    }

    get(): Observable<TriMetaDataInterface> {
        return this.api.get(`${this.apiController}`)
            .pipe(
                map(response => {
                    const dto = response as DtoModel<string>;

                    this.checkResponse(dto as DtoModel<any>);

                    const jsonPayload = JSON.parse(dto.value) as TriMetaDataInterface;

                    return jsonPayload;
                }),
            );
    }
}