import { Injectable } from "@angular/core";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { IResourceAssignRequest, IUserResourceModel, ResourceMasterModel, UserResourceModel } from "@TriInterfaces/rsu/rsu.user-resource.interfaces";

@Injectable()
export class TriUserResourceApi extends TriBaseApi<any> {

    constructor(private api: HttpService) {
        super();
    }

    // TODO: change types for this 
    getResourcesForThisDealerFull(): Promise<any> {
        return this.api.get(`Nuvol9Api/UserResource/Json`).toPromise().then((result) => {
            // TODO: change DtoModel<any> to correct type
            const dto = result as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            const resultData = JSON.parse(dto.value) as UserResourceModel[][];
            return resultData;
        })
           
    }

    getResourcesForThisDealer(): Promise<IUserResourceModel[]> {
        return this.api.get(`Nuvol9Api/UserResource`).toPromise().then((result) => {
            // TODO: change DtoModel<any> to correct type
            const dto = result as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value as IUserResourceModel[];
        })
	}

    saveUser(save: IResourceAssignRequest): Promise<ResourceMasterModel> {
        return this.api.post(`Nuvol9Api/UserResource/Json`, save).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log(dto);
            this.checkResponse(dto as DtoModel<any>);
            // return dto.value;
            return JSON.parse(dto.value) as ResourceMasterModel;
        })
    }

    getResourceByGpEmployeeId(id: string): Promise<any> {
        return this.api.get(`Nuvol9Api/UserResource/GetByGpEId/${id}`).toPromise().then((result) => {
            // TODO: change DtoModel<any> to correct type
            const dto = result as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }

}