import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

import { TriAddressResponseInterface, TriVerifyAddressRequestInterface } from '@TriInterfaces/qal/qal.address.interfaces';
import { map } from "rxjs/operators";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";

@Injectable()
export class TriAddressVerificationApi extends TriBaseApi<TriAddressResponseInterface> {
    private readonly apiController = 'Nuvol9Api/AddressVerification';

    constructor(private api: HttpService) {
        super();
    }

    verify(payload: TriVerifyAddressRequestInterface): Promise<TriAddressResponseInterface> {

        return this.api.post(`${this.apiController}/Verify`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<TriAddressResponseInterface>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }

    verifySimple(payload: TriVerifyAddressRequestInterface): Promise<TriAddressResponseInterface> {
        return this.api.post(`${this.apiController}/VerifySimple`, payload as TriVerifyAddressRequestInterface).toPromise().then((data) => {
            const dto = data as DtoModel<TriAddressResponseInterface>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }

    verifyLong(payload: TriVerifyAddressRequestInterface): Promise<TriAddressResponseInterface> {
        return this.api.post(`${this.apiController}/VerifyLong`, payload as TriVerifyAddressRequestInterface).toPromise().then((data) => {
            const dto = data as DtoModel<TriAddressResponseInterface>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }

    saveAddressRaw(payload: TriVerifyAddressRequestInterface): Promise<TriAddressResponseInterface> {
        return this.api.post(`${this.apiController}/SaveRaw`, payload as TriVerifyAddressRequestInterface).toPromise().then((data) => {
            const dto = data as DtoModel<TriAddressResponseInterface>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }

}