import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { MasterFileAccountCancelRequestModel } from "@TriInterfaces/ace/ace.aceRoot.interfaces";
import { ProductSkwFullModel } from "@TriInterfaces/ace/ace.productSkw.interfaces";
import { IDmeMasterFileAccountDocumentModel } from "@TriInterfaces/dme/dme.root.interfaces";
import { TriAceWorkOrderApi } from "app/@core/backend/common/api/ace/aceWorkOrder.api";
import { TriAceRootApi } from "app/@core/backend/common/api/ace/triAceRoot.api";
import { TriDmeRootApi } from "app/@core/backend/common/api/dme/triDmeRoot.api";
import { TriMasterFileAccountApi } from "app/@core/backend/common/api/mas/triMasterFileAccount.api";
import { GetMfaDocumentsAction } from "./dme.actions";

export interface TriDmeStateModel {
    mfaDocuments : IDmeMasterFileAccountDocumentModel[],
}

const getInitialModel = (): TriDmeStateModel => ({
    mfaDocuments: []
});

@Injectable()
@State<TriDmeStateModel>({
    name: 'TriDmeStateRoot',
    defaults: getInitialModel(),
})
export class TriDmeState {
    constructor(private triDmeApi: TriDmeRootApi) { }

    @Selector()
    static getMfaDocumentsData({ mfaDocuments }: TriDmeStateModel) {
        return mfaDocuments;
    }


    @Action(GetMfaDocumentsAction)
    getMfaDocuments(
        ctx: StateContext<TriDmeStateModel>,
        action: GetMfaDocumentsAction
    ) {
        const payload: string = action.payload;

        this.triDmeApi.getMfaDocuments(payload).then(
            (response: any) => {
                const state = ctx.getState();

                let resultant = ctx.patchState({
                    ...state,
                    mfaDocuments: response,
                });

                return resultant;
            }
        );

    }
}