import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { IADCCheckLoginNameRequest } from "@TriInterfaces/mas/mas.alarm-com.interfaces";
import { ReceiverRootMetaDataModel } from "@TriInterfaces/mas/mas.root.interfaces";
import { TriMasRootApi } from "app/@core/backend/common/api/mas/triMasRoot.api";
import { CheckLoginNameAction, GetADCDealerMetaDataResoucesAction, GetReceiverMetaDataResoucesAction, SaveIndustryNumberAction } from "./triMasRoot.action";

export interface TriIndustryDialogStateModel {
    metaDataResources: ReceiverRootMetaDataModel;
    industryNumber: any;
    adcDealerMetaData: any;
}

const getInitialModel = (): TriIndustryDialogStateModel => ({
    metaDataResources: null,
    industryNumber: null,
    adcDealerMetaData: null
});

@Injectable()
@State<TriIndustryDialogStateModel>({
    name: 'TriIndustryRoot',
    defaults: getInitialModel(),
})

export class TriIndustryDialogState {
    constructor(private masApi: TriMasRootApi) { }

    @Selector()
    static getReceiverMetaDataResouces({ metaDataResources }: TriIndustryDialogStateModel) {
        return metaDataResources;
    }

    @Selector()
    static getAdcDealerMetaData({ adcDealerMetaData }: TriIndustryDialogStateModel) {
        return adcDealerMetaData;
    }

    @Action(GetReceiverMetaDataResoucesAction)
    getReceiverMetaDataResouces(
        ctx: StateContext<TriIndustryDialogStateModel>,
        action: GetReceiverMetaDataResoucesAction
    ) {
        this.masApi.getReceiverMetaDataResouces().then((response) => {
            console.log("metadata action", response)
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                metaDataResources: response,
            });
            return resultant;
        })
    }

    @Action(GetADCDealerMetaDataResoucesAction)
    getADCDealerMetaData(
        ctx: StateContext<TriIndustryDialogStateModel>,
        action: GetADCDealerMetaDataResoucesAction
    ) {
        this.masApi.getADCDealerMetaData().then((response) => {
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                adcDealerMetaData: response,
            });
            return resultant;
        })
    }

    @Action(CheckLoginNameAction)
    checkLoginName(
        ctx: StateContext<TriIndustryDialogStateModel>,
        action: CheckLoginNameAction
    ) {
        const payload: IADCCheckLoginNameRequest = action.payload;
        this.masApi.checkLoginName(payload).then((response) => {
            const state = ctx.getState();
            return response;
        })
    }

    @Action(SaveIndustryNumberAction)
    saveIndustryNumber(
        ctx: StateContext<TriIndustryDialogStateModel>,
        action: SaveIndustryNumberAction
    ) {
        let payload: string = action.payload;
        this.masApi.saveIndustryNumber(payload).then((data) => {
            const state = ctx.getState();
            let resultant = ctx.patchState({
                ...state,
                industryNumber: data,
            });
            //console.log("RESPONSE 2: ", resultant);
            return resultant;
        });
    }
}