export class DtoModel<T> {
    public initialEnvelopID: string;
    public dtoMessage: string;
    public code: DtoMessageCodes;
    public objectSignature: string;
    public rawJson: string;
    public slackNotify: boolean;
    public isJson: boolean;

    public value: T;
}

