import { Injectable } from "@angular/core";
import { TriCustomerBillingSummaryInterface, TriCustomerPaymentItemInterface } from "@TriInterfaces/ace/ace.customer-root.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

@Injectable()
export class TriCustomerPaymentInvoiceApi extends TriBaseApi<TriCustomerPaymentItemInterface> {
    private readonly apiController: string = 'Nuvol9Api/PaymentInvoice';

    constructor(private api: HttpService) {
        super();
    }

    getCustomerBillingSummary(id: number): Promise<TriCustomerBillingSummaryInterface[]> {
        return this.api.get(`${this.apiController}/${id}/Summary`).toPromise().then((result) => {
            const dto = result as DtoModel<string>;

            this.checkResponse(dto as DtoModel<any>);

            /**
             * This endpoint returns a stringified JSON object
             */
            const response = JSON.parse(dto.value) as TriCustomerBillingSummaryInterface[];
            return response;
        })
    }

    getCustomerInvoiceHistory(id: number): Promise<any[]> {
        return this.api.get(`${this.apiController}/History?id=${id}`).toPromise().then((result) => {
            const dto = result as DtoModel<string>;

            this.checkResponse(dto as DtoModel<any>);

            //console.log("Invoice History: ", dto.value);
            return (dto.value) as any;
        })
    }

    getRoutingBankInfo(rid: string): Promise<any> {
        return this.api.get(`Nuvol9Api/AchRouterNumber/Lookup?rid=${rid}`).toPromise().then((result) => {
            const dto = result as DtoModel<string>;

            this.checkResponse(dto as DtoModel<any>);

            console.log("Routing Bank Info: ", dto.value);
            return (dto.value) as any;
        })
    }
}