import { Injectable } from "@angular/core";
import { TriCustomerAgingInterface } from "@TriInterfaces/ace/ace.customer-root.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

@Injectable()
export class TriCustomerAgingApi extends TriBaseApi<TriCustomerAgingInterface> {
    private readonly apiController: string = 'Nuvol9Api/Aging';


    constructor(private api: HttpService) {
        super();
    }

    getCustomerAging(id: number): Observable<TriCustomerAgingInterface[]> {
        return this.api.get(`${this.apiController}/${id}`)
            .pipe(
                map((response, ndx) => {
                  const dto = response as DtoModel<TriCustomerAgingInterface[]>;

                  this.checkResponse(dto as DtoModel<any>);

                  return dto.value;
                })
            );
    }
}