import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext, Store } from "@ngxs/store";
import { TriMetaDataInterface } from "@TriInterfaces/gui/gui.metadata.interfaces";
import { TriGuiMetaDataApi } from "app/@core/backend/common/api/gui/triMetadata.api";
import { tap } from "rxjs/operators";
import { GetGuiMetadata } from "./gui.actions";

export class TriGuiStateModel {
    metadata: TriMetaDataInterface
}

@Injectable()
@State<TriGuiStateModel>({
    name: 'guiMetadata',
    defaults: {
        metadata: null
    }
})
export class TriGuiState {

    @Selector()
    static getPoliticalStates({ metadata }: TriGuiStateModel) {
        return metadata.mac.politicalStates.filter(item => item.id !== '00');
    }

    @Selector()
    static getPoliticalTimeZones({ metadata }: TriGuiStateModel) {
        return metadata.mac.politicalTimeZones;
    }

    @Selector()
    static getPoliticalStateAndTimeZoneMaps({ metadata }: TriGuiStateModel) {
        return metadata.mac.politicalStateAndTimeZoneMaps;
    }

    constructor(private api: TriGuiMetaDataApi, private store: Store) {
    }

    @Action(GetGuiMetadata)
    loadMetadata({getState, setState}: StateContext<TriGuiStateModel>) {
        return this.api.get()
            .pipe(
                tap(result => {
                    const state = getState();

                    setState({
                        ...state,
                        metadata: result,
                    });
                }),
            );
    }
}