import { Injectable } from "@angular/core";
import { IADCCheckLoginNameRequest } from "@TriInterfaces/mas/mas.alarm-com.interfaces";
import { ReceiverRootMetaDataModel } from "@TriInterfaces/mas/mas.root.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";



@Injectable()
export class TriMasRootApi extends TriBaseApi<any>{
    static getReceiverMetaDataResouces: any;
    static ReceiverRootMetaDataModel: any;
    constructor(private api: HttpService) {
        super();
    }

    public getReceiverMetaDataResouces() {
        return this.api.get(`Nuvol9Api/ReceiverLine/MetaData`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value) as ReceiverRootMetaDataModel;
        })
    }

    public getADCDealerMetaData() {
        return this.api.get(`Nuvol9Api/AlarmCom/MetaData`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value);
        })
    }
    


    public checkLoginName(data: IADCCheckLoginNameRequest): Promise<any>  {
        return this.api.put(`Nuvol9Api/AlarmCom/CheckLoginName`, data).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            return dto.value;
        })
    }

    saveIndustryNumber(payload: any): Promise<any> {
        let input = { "jsonInput": JSON.stringify(payload) }
        return this.api.put(`Nuvol9Api/CentralStation/AssignLinePosition`, input).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log("ACCOUNT HOLD FIX DTO: ",dto);
            this.checkResponse(dto as DtoModel<any>);
            return dto.value;
        })
    }
}