import { Injectable } from "@angular/core";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { AppointmentModel, SceMetaData } from "@TriInterfaces/sce/sce.root.interfaces";
import { TriCustomerTicketInterface } from "@TriInterfaces/sce/sce.tickets.interfaces";
import { CustomerMasterModel } from "@TriInterfaces/ace/ace.customer-master.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";

@Injectable()
export class TriSchRootApi extends TriBaseApi<AppointmentModel> {
    private readonly apiController = 'Nuvol9Api/Appointment/GetOrgSch';

    constructor(private api: HttpService) {
        super();
    }  


    getOrgSchedule(payload: any): Promise<any> {
        return this.api.get(`${this.apiController}`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            console.log(dto);
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value);
        });
    }

}