import { Injectable } from "@angular/core";
import { TriCustomerPaymentItemInterface } from "@TriInterfaces/ace/ace.customer-root.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { TransResponseModel } from "@TriInterfaces/mch/mch.tran-response.interfaces";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

@Injectable()
export class TriPaymentApi extends TriBaseApi<TriCustomerPaymentItemInterface> {

    constructor(private api: HttpService) {
        super();
    }

    getTranHistory(masterFileId: number, masterFileAccountId: string): Promise<TransResponseModel[]> {
        return this.api.get(`Nuvol9Api/CcTransaction/History?id=${masterFileId}&aid=${masterFileAccountId}`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;
            //console.log(dto);

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }
}