import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { IndustryNumber } from "@TriInterfaces/mas/mas.industry-number.interfaces";
import { GetIndustryNumbers, IssueIndustryNumber } from "./account.actions";
import { TriAccountApi } from "app/@core/backend/common/api/mas/triAccount.api";

export interface TriAccountStateModel {
    industryNumbers: IndustryNumber[]
}

const getInitialModel = (): TriAccountStateModel => ({
    industryNumbers: [],
});

@Injectable()
@State<TriAccountStateModel>({
    name: 'TriAccountRoot',
    defaults: getInitialModel(),
})
export class TriAccountState {
    constructor(private accountApi: TriAccountApi) { }

    @Selector()
    static getIndustryNumbersForAccount({ industryNumbers }: TriAccountStateModel) {
        return industryNumbers;
    }

    @Action(GetIndustryNumbers)
    getIndustryNumbers(
        ctx: StateContext<TriAccountStateModel>,
        action: GetIndustryNumbers
    ) {
        const masterFileAccountId: string = action.masterFileAccountId;
        this.accountApi.getIndustryNumbers(masterFileAccountId).then((response) => {
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                industryNumbers: response,
            });
            return resultant;
        })
    }

    @Action(IssueIndustryNumber)
    issueIndustryNumber(
        ctx: StateContext<TriAccountStateModel>,
        action: IssueIndustryNumber
    ) {
        const payload: any = action.payload;
        // this.accountApi.getIndustryNumbers(masterFileAccountId).then((response) => {
        //     const state = ctx.getState();

        //     let resultant = ctx.patchState({
        //         ...state,
        //         industryNumbers: response,
        //     });
        //     return resultant;
        // })
        this.accountApi.issueIndustryNumber(payload).then((response) => {
            // const state = ctx.getState();

            // let resultant = ctx.patchState({
            //     ...state,
            //     industryNumbers: response,
            // });
            // return resultant;
            console.log("resposne", response);
        })
    }
}