import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { patch, removeItem } from "@ngxs/store/operators";
import { ProductSkwFullModel } from "@TriInterfaces/ace/ace.productSkw.interfaces";
import { IMasEquipmentWithMetaData, MasEquipmentItem } from "@TriInterfaces/mas/mas.equipment.interfaces";
import { EquipmentRootMetaDataModel, MasAccountMetaDataModel } from "@TriInterfaces/mas/mas.root.interfaces";
import { TriAceRootApi } from "app/@core/backend/common/api/ace/triAceRoot.api";
import { TriAccountApi } from "app/@core/backend/common/api/mas/triAccount.api";
import { TriAccountEquipmentApi } from "app/@core/backend/common/api/mas/triAccountEquipment.api";
import { DeleteEquipmentAction, GetAlarmProductList, GetEquipmentByBarcode, GetEquipmentData, GetEquipmentMetData, GetEquipmentMetDataResources, GetMasAccountMetaData, SaveSKWData } from "./skwModal.actions";

export interface TriSkwMasterStateModel {
    alarmProductList: ProductSkwFullModel[],
    equipmentMetadata: IMasEquipmentWithMetaData,
    accountMetadata: MasAccountMetaDataModel,
    equipmentData: MasEquipmentItem[],
    equipmentByBarcode: IMasEquipmentWithMetaData[],
    equipmentMetadataResources: EquipmentRootMetaDataModel
}

const getInitialModel = (): TriSkwMasterStateModel => ({
    alarmProductList: null,
    equipmentMetadata: null,
    equipmentData: null,
    accountMetadata: null,
    equipmentByBarcode: null,
    equipmentMetadataResources: null
});

@Injectable()
@State<TriSkwMasterStateModel>({
    name: 'TriSkwMasterRoot',
    defaults: getInitialModel(),
})
export class TriSkwModalState {
    constructor(private triAceRootApi: TriAceRootApi, private triAccountEquipApi: TriAccountEquipmentApi, private triAccountApi: TriAccountApi) { }

    @Selector()
    static getAlarmProductListData({ alarmProductList }: TriSkwMasterStateModel) {
        return alarmProductList;
    }

    @Selector()
    static getEquipmentData({ equipmentData }: TriSkwMasterStateModel) {
        return equipmentData;
    }

    @Selector()
    static getEquipmentMetaDataResources({ equipmentMetadataResources }: TriSkwMasterStateModel) {
        return equipmentMetadataResources;
    }

    @Selector()
    static getEquipmentMetdataData({ equipmentMetadata }: TriSkwMasterStateModel) {
        return equipmentMetadata;
    }

    @Selector()
    static getEquipmentByBarcode({ equipmentByBarcode }: TriSkwMasterStateModel) {
        return equipmentByBarcode;
    }

    @Selector()
    static getAccountMetadataData({ accountMetadata }: TriSkwMasterStateModel) {
        return accountMetadata;
    }

    @Action(GetAlarmProductList)
    getAlarmProductList(
        ctx: StateContext<TriSkwMasterStateModel>,
        action: GetAlarmProductList
    ) {
        let id: string = action.id;
        this.triAceRootApi.getAlarmProductList(id).then(
            (response: any) => {
                const state = ctx.getState();

                let resultant = ctx.patchState({
                    ...state,
                    alarmProductList: response,
                });

                return resultant;
            }
        );

        }

    @Action(GetEquipmentData)
    getEquipmentData(
        ctx: StateContext<TriSkwMasterStateModel>,
        action: GetEquipmentData
    ) {
        let id: number = action.masAccountId;
        this.triAccountEquipApi.getEquipment(id).then(
            (response: any) => {
                const state = ctx.getState();

                let resultant = ctx.patchState({
                    ...state,
                    equipmentData: response,
                });

                return resultant;
            }
        );

    }

    @Action(SaveSKWData)
    save(
        ctx: StateContext<TriSkwMasterStateModel>,
        action: SaveSKWData
    ) {
        let request: string = action.request;

        this.triAccountEquipApi.save(request).then((response) => {
            // TODO: update state?
            return;
        })
    }

    @Action(GetMasAccountMetaData)
    getMasAccountMetaData(
        ctx: StateContext<TriSkwMasterStateModel>,
        action: GetMasAccountMetaData
    ) {

        this.triAccountApi.getMasAccountMetaData().then(
            (response: any) => {
                const state = ctx.getState();

                let resultant = ctx.patchState({
                    ...state,
                    accountMetadata: response,
                });

                return resultant;
            }
        );

    }

    @Action(DeleteEquipmentAction)
    deleteEquipment(
        ctx: StateContext<TriSkwMasterStateModel>,
        action: DeleteEquipmentAction
    ) {
        const id = action.id;
        const mfaId = action.mfaId;

        this.triAccountEquipApi.delete(id, mfaId).then(
            (response: any) => {
                console.log("deleted: ", response);

                let resultant = ctx.setState(
                    patch<TriSkwMasterStateModel>({
                        equipmentData: removeItem<MasEquipmentItem>(equipment => equipment.id === response[0].masAccountEquipmentID)
                    })
                );

                return resultant;
            }
        );

    }

    @Action(GetEquipmentMetData)
    getEquipmentMetdata(
        ctx: StateContext<TriSkwMasterStateModel>,
        action: GetEquipmentMetData
    ) {
        let skw: string = action.skw;
        this.triAccountEquipApi.getEquipmentBySkw(skw).then(
            (response: any) => {
                const state = ctx.getState();

                let resultant = ctx.patchState({
                    ...state,
                    equipmentMetadata: response,
                });

                return resultant;
            }
        );
    }


    @Action(GetEquipmentMetDataResources)
    getEquipmentMetaDataResources(
        ctx: StateContext<TriSkwMasterStateModel>,
        action: GetEquipmentMetDataResources
    ) {
        this.triAccountEquipApi.getEquipmentMetaDataResources().then(
            (response: any) => {
                const state = ctx.getState();

                let resultant = ctx.patchState({
                    ...state,
                    equipmentMetadataResources: response,
                });

                return resultant;
            }
        );
    }
    

    @Action(GetEquipmentByBarcode)
    getEquipmentByBarcode(
        ctx: StateContext<TriSkwMasterStateModel>,
        action: GetEquipmentByBarcode
    ) {
        let barcode: string = action.barcode;
        let bypass: boolean = action.bypass;
        this.triAccountEquipApi.getEquipmentByBarcode(barcode, bypass).then(
            (response: any) => {
                const state = ctx.getState();

                let resultant = ctx.patchState({
                    ...state,
                    equipmentByBarcode: response,
                });

                return resultant;
            }
        );
    }

}