import { Injectable } from "@angular/core";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { IInvoiceRequestModel } from "@TriInterfaces/ace/ace.invoice.interfaces";

@Injectable()
export class TriAceInvoiceRootApi extends TriBaseApi<any> {

    constructor(private api: HttpService) {
        super();
    }

    invoiceItemCreate(data: any): Promise<any> {
        return this.api.post(`Nuvol9Api/InvoiceItem`, data).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })      
    }

    masInvoiceCreate(data: IInvoiceRequestModel): Promise<any> {
        return this.api.post(`Nuvol9Api/Invoice`, data).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })      
    }
}