import { Injectable } from "@angular/core";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { ProductSkwFullModel } from "@TriInterfaces/ace/ace.productSkw.interfaces";
import { AceRootMetaDataModel, ProductRootMetaDataModel } from "@TriInterfaces/ace/ace.aceRoot.interfaces";

@Injectable()
export class TriAceRootApi extends TriBaseApi<any> {

    constructor(private api: HttpService) {
        super();
    }

    getAlarmProductList(id: string): Promise<ProductSkwFullModel[]> {
        return this.api.get(`Nuvol9Api/ProductType/${id}/ProductSkws`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            console.log("boop", JSON.parse(dto.value) as ProductSkwFullModel[])
            return JSON.parse(dto.value) as ProductSkwFullModel[];
        })      
    }


    getAceMetadata(): Promise<AceRootMetaDataModel[]> {
        return this.api.get(`Nuvol9Api/MasterFile/GetMetaData`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value) as AceRootMetaDataModel[];
        })      
    }

    getProdMetaDataResources(): Promise<ProductRootMetaDataModel> {
        return this.api.get(`Nuvol9Api/ProductSkw/GetMetaData`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value as ProductRootMetaDataModel;
        })      
    }

    getProductSkwById(id: number): Promise<any> {
        return this.api.get(`Nuvol9Api/ProductSkw/${id}`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })      
    }
}