import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";

import { TriCustomerNoteInterface } from '@TriInterfaces/mac/mac.notes-tickets-tasks.interfaces';
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { IAccountNoteRequest, IAccountNoteResponse, IAccountNoteRootModel } from "@TriInterfaces/mac/mac.account-note.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";

@Injectable()
export class TriCustomerNotesApi extends TriBaseApi<TriCustomerNoteInterface> {
    private readonly apiController: string = 'Nuvol9Api/AccountNote/GetMetaData';

    constructor(private api: HttpService) {
        super();
    }

    getNotesByIds(mfId: number, mfaId?: number, cId?: number, lId?: number): Observable<TriCustomerNoteInterface[]> {
        let params = `mfId=${mfId}`

        if (mfaId) params = `${params}&mfaId=${mfaId}`;
        if (cId) params = `${params}&cId=${cId}`;
        if (lId) params = `${params}&lId=${lId}`;

        return this.api.get(`${this.apiController}/GetAccountNotes?${params}`)
            .pipe(
                map(response => {
                    const dto = response as DtoModel<TriCustomerNoteInterface[]>;

                    this.checkResponse(dto as DtoModel<any>);

                    return dto.value;
                }),
            );
    }
    getNoteDepartment(): Promise<IAccountNoteRootModel> {
        return this.api.get(`${this.apiController}`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log(dto)
            this.checkResponse(dto as DtoModel<any>);
            // return dto.value;
            return dto.value;
        })
    }
    saveNote(save: IAccountNoteRequest): Promise<IAccountNoteResponse> {
        return this.api.post(`Nuvol9Api/AccountNote/AccountNotes`, save).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log(dto)
            this.checkResponse(dto as DtoModel<any>);
            // return dto.value;
            return dto.value;
        })
    }

    getNotes(mfId: number, mfaId: string, cId: number, lId: number): any {
        return this.api.get(`Nuvol9Api/AccountNote/GetAccountNotes?mfId=${mfId}&mfaId=${mfaId}&cId=${cId}&lId=${lId}`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log(dto)
            this.checkResponse(dto as DtoModel<any>);
            // return dto.value;
            return dto.value;
        })
    }

}