import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext, Store } from "@ngxs/store";
import { TriSystemInfoApi } from "app/@core/backend/common/api/mas/triSystemInfo.api";
import { IAGCodeword, IAGContact, IAGDevice, IAGDispatchType, IAGHistoryItem, IAGSystemInfo } from "@TriInterfaces/mas/mas.avant-guard.interfaces";
import { GetCsAvantGuardInfoAction, ZoneVerificationAction, GetCsAvantGuardSignalHistoryAction } from "./system-info.actions";
import { DeleteEmergencyContactAction, GetContactAction, GetContactListAction, SaveEmergencyContactAction } from "./securenet.actions";
import { SecureNetContactModel } from "@TriInterfaces/mas/mas.secure-net.interfaces";
import { TriSecureNetApi } from "app/@core/backend/common/api/mas/triSecurenet.api";
import { EmergencyContactModel } from "@TriInterfaces/mas/mas.emergency-contact.interfaces";

export interface TriSecureNetStateModel {
    contacts: SecureNetContactModel[],
    contact: any
}

@Injectable()
    @State<TriSecureNetStateModel>({
        name: 'TriSecureNet',
        defaults: {
            contacts: null,
            contact: null
        }
})
export class TriSecureNetState {
    constructor(private triSecureNetApi: TriSecureNetApi) { }

    @Selector()
    static getContactListData({ contacts }: TriSecureNetStateModel) {
        return contacts;
    }

    @Selector()
    static getContactData({ contact }: TriSecureNetStateModel) {
        return contact;
    }


    @Action(GetContactListAction)
    getCsAvantGuardInfo(
        ctx: StateContext<TriSecureNetStateModel>,
        action: GetContactListAction
    ) {
        let id: number = action.accountId;
        
        this.triSecureNetApi.getContactList(id).then((data) => {
            const state = ctx.getState();
            let resultant = ctx.patchState({
                ...state,
                contacts: data,
            });

            return resultant;
        })
    }

    @Action(GetContactAction)
    getContact(
        ctx: StateContext<TriSecureNetStateModel>,
        action: GetContactAction
    ) {
        let id: number = action.id;
        let cid: string = action.cId;
        
        this.triSecureNetApi.getContact(id, cid).then((data) => {
            const state = ctx.getState();
            let resultant = ctx.patchState({
                ...state,
                contacts: data,
            });

            return resultant;
        })
    }
    
    @Action(SaveEmergencyContactAction)
    saveEmergencyContact(
        ctx: StateContext<TriSecureNetStateModel>,
        action: SaveEmergencyContactAction
    ) {
        let payload: EmergencyContactModel = action.data;
        
        this.triSecureNetApi.saveEmergencyContact(payload).then((data) => {
            return;
        })
    }


    @Action(DeleteEmergencyContactAction)
    deleteEmergencyContact(
        ctx: StateContext<TriSecureNetStateModel>,
        action: DeleteEmergencyContactAction
    ) {
        let id: string = action.id;
        
        this.triSecureNetApi.deleteEmergencyContact(id).then((data) => {
            return;
        })
    }
}