import { Injectable } from "@angular/core";
import { IDmeMasterFileAccountDocumentModel } from "@TriInterfaces/dme/dme.root.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

@Injectable()
export class TriDmeRootApi extends TriBaseApi<any> {

    constructor(private api: HttpService) {
        super();
    }

    getMfaDocuments(mfaId: string): Promise<IDmeMasterFileAccountDocumentModel[]> {
        return this.api.get(`Nuvol9Api/MasterFileAccount/${mfaId}/MasterFileAccountDocuments`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value) as IDmeMasterFileAccountDocumentModel[];
        })      
    }
}