import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { MasterFileAccountCancelRequestModel, ProductRootMetaDataModel } from "@TriInterfaces/ace/ace.aceRoot.interfaces";
import { ProductSkwFullModel } from "@TriInterfaces/ace/ace.productSkw.interfaces";
import { TriAceRootApi } from "app/@core/backend/common/api/ace/triAceRoot.api";
import { TriMasterFileAccountApi } from "app/@core/backend/common/api/mas/triMasterFileAccount.api";
import { GetAceMetadata, GetProdMetaDataResourcesAction, GetProductSkwByIdHandlerAction, PostCancelAccount } from "./cancelAccount.actions";
import { GetAlarmProductList } from "./skwModal.actions";

export interface TriCancelAccountMasterStateModel {
    aceMetadata: ProductSkwFullModel[],
    prodMetaDataResources: ProductRootMetaDataModel,
    prodSkwData: any
}

const getInitialModel = (): TriCancelAccountMasterStateModel => ({
    aceMetadata: null,
    prodMetaDataResources: null,
    prodSkwData: null
});

@Injectable()
@State<TriCancelAccountMasterStateModel>({
    name: 'TriCancelAccountMasterRoot',
    defaults: getInitialModel(),
})
export class TriCancelAccountState {
    constructor(private triAceRootApi: TriAceRootApi, private triMasterFileApi: TriMasterFileAccountApi) { }

    @Selector()
    static getAceMetdataData({ aceMetadata }: TriCancelAccountMasterStateModel) {
        return aceMetadata;
    }

    @Selector()
    static getProdMetaDataResourcesData({ prodMetaDataResources }: TriCancelAccountMasterStateModel) {
        return prodMetaDataResources;
    }

    @Selector()
    static getProdSkwData({ prodSkwData }: TriCancelAccountMasterStateModel) {
        return prodSkwData;
    }


    @Action(GetAceMetadata)
    getAceMetadata(
        ctx: StateContext<TriCancelAccountMasterStateModel>,
        action: GetAceMetadata
    ) {
        this.triAceRootApi.getAceMetadata().then(
            (response: any) => {
                const state = ctx.getState();

                let resultant = ctx.patchState({
                    ...state,
                    aceMetadata: response,
                });

                return resultant;
            }
        );

    }

    @Action(GetProdMetaDataResourcesAction)
    getProdMetaDataResources(
        ctx: StateContext<TriCancelAccountMasterStateModel>,
        action: GetProdMetaDataResourcesAction
    ) {
        this.triAceRootApi.getProdMetaDataResources().then(
            (response: any) => {
                const state = ctx.getState();

                let resultant = ctx.patchState({
                    ...state,
                    prodMetaDataResources: response,
                });

                return resultant;
            }
        );

    }

    @Action(GetProductSkwByIdHandlerAction)
    getProductSkwById(
        ctx: StateContext<TriCancelAccountMasterStateModel>,
        action: GetProductSkwByIdHandlerAction
    ) {
        const id : number = action.id;
        this.triAceRootApi.getProductSkwById(id).then(
            (response: any) => {
                const state = ctx.getState();

                let resultant = ctx.patchState({
                    ...state,
                    prodMetaDataResources: response,
                });

                return resultant;
            }
        );

    }


    @Action(PostCancelAccount)
    cancelAccount(
        ctx: StateContext<TriCancelAccountMasterStateModel>,
        action: PostCancelAccount
    ) {
        let payload: MasterFileAccountCancelRequestModel = action.payload;

        this.triMasterFileApi.cancelAccount(payload).then((data) => {
            console.log("completed", data)
        })

    }

}