import { Injectable } from "@angular/core";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { AppointmentModel, SceMetaData } from "@TriInterfaces/sce/sce.root.interfaces";
import { TriCustomerTicketInterface } from "@TriInterfaces/sce/sce.tickets.interfaces";
import { CustomerMasterModel } from "@TriInterfaces/ace/ace.customer-master.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";

@Injectable()
export class TriSceRootApi extends TriBaseApi<AppointmentModel> {
    private readonly apiController = 'Nuvol9Api/MasterFile';

    constructor(private api: HttpService) {
        super();
    }

    getSceMetaData(): Promise<SceMetaData> {
        return this.api.get(`Nuvol9Api/Appointment/GetMetaData`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log(dto);
            this.checkResponse(dto as DtoModel<any>);
            // return dto.value;
            return JSON.parse(dto.value) as SceMetaData;
        });
        
    }

    getAppointmentsByMfId(id: number): Promise<AppointmentModel> {
        return this.api.get(`${this.apiController}/${id}/Appointments`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log(dto);
            this.checkResponse(dto as DtoModel<any>);
            // return dto.value;
            return JSON.parse(dto.value) as AppointmentModel;
        });
    }

    getAppointmentsByMfIdWithHistory(id: number): Promise<AppointmentModel> {
        return this.api.get(`${this.apiController}/${id}/AppointmentsWithHistory`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log(dto);
            this.checkResponse(dto as DtoModel<any>);
            // return dto.value;
            return JSON.parse(dto.value) as AppointmentModel;
        });
    }

    saveAppointment(data: any, returnSingleSavedObject: boolean = false): Promise<AppointmentModel> {
        let payload = { "jsonInput": data };
        return this.api.post(`Nuvol9Api/Appointment/`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<any>; // update model
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value) as AppointmentModel;
        });
    }

    deleteAppointment(id: any): Promise<AppointmentModel> {
        return this.api.delete(`Nuvol9Api/Appointment/?id=${id}`, id).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value) as AppointmentModel;
        });
    }

    taskCreate(data: any): Promise<any> {
        let payload = { "jsonInput": data };
        return this.api.post(`Nuvol9Api/Tasks`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<any>; // update model
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value);
        });
    }

    getTasksByMfId(id: number): Promise<any> {
        return this.api.get(`${this.apiController}/${id}/Tasks`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log(dto);
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value) as CustomerMasterModel;
            // return JSON.parse(dto.value) as AppointmentModel;
        });
    }

    getTasksByMfIdWithDone(id: number): Promise<any> {
        return this.api.get(`${this.apiController}/${id}/TasksWithDone`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log(dto);
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value) as CustomerMasterModel;
            // return JSON.parse(dto.value) as AppointmentModel;
        });
    }

    closeTask(data: any): Promise<any> {
        let payload = { "jsonInput": data };
        return this.api.post(`Nuvol9Api/Tasks/Close`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value);
        });
    }

    getTicketsByMfaId(id: number): Promise<any> {
        return this.api.get(`Nuvol9Api/MasterFile/${id}/Tickets`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            //return dto.value;
            return JSON.parse(dto.value) as TriCustomerTicketInterface;
        });
    }

    getTicketsByMfaIdWithClosed(id: number): Promise<any> {
        return this.api.get(`Nuvol9Api/MasterFile/${id}/TicketsWithClosed`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            //return dto.value;
            return JSON.parse(dto.value) as TriCustomerTicketInterface;
        });
    }

    saveTicket(data: any): Promise<any> {
        let payload = { "jsonInput": data };
        return this.api.post(`Nuvol9Api/Tickets`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value);
        });
    }

    taskNoteCreate(data: any): Promise<any> {
        let payload = { "jsonInput": data };
        return this.api.post(`Nuvol9Api/TaskNotes`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value);
        });
    }

    closeTicket(data: any): Promise<any> {
        let payload = { "jsonInput": data };
        return this.api.put(`Nuvol9Api/Tickets/Close`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value);
        });
    }

    addMemoTicket(data: any): Promise<any> {
        let payload = { "jsonInput": data };
        return this.api.post(`Nuvol9Api/TicketNotes`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value);
        });
    }

    addCommentTicket(data: any): Promise<any> {
        let payload = { "jsonInput": data };
        return this.api.post(`Nuvol9Api/TicketComments`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value);
        });
    }
}