export interface IAppointment extends INvl9BaseModel {
    id: number;
    appointmentTypeId: string;
    masterFileId?: number;
    masterFileAccountId?: string;
    dealerId: number;
    leadId?: number;
    customerId?: number;
    title: string;
    description?: string;
    url?: string;
    allDay: boolean;
    startDateTime: Date;
    endDateTime: Date;
    reminders?: IAppointmentReminder[];
}

/** DEFAULT Appointment Model */
export class AppointmentModel implements IAppointment {
    id: number;
    appointmentTypeId: string;
    masterFileId?: number;
    masterFileAccountId?: string;
    dealerId: number;
    leadId?: number;
    customerId?: number;
    title: string;
    description?: string;
    url?: string;
    allDay: boolean;
    startDateTime: Date;
    endDateTime: Date;
    lbStartDateTime: Date;
    lbEndDateTime: Date;
    reminders?: IAppointmentReminder[];
    isActive?: boolean;
    modifiedBy?: IUserInfoModel;
    modifiedOn?: Date;
    createdBy?: IUserInfoModel;
    createdOn?: Date;
    ptz: IPoliticalTimeZoneResponseModel;

    //** CONSTRUCTORS */
    constructor(private input: IAppointment, ptz: IPoliticalTimeZoneResponseModel) {
        this.id = input.id;
        this.appointmentTypeId = input.appointmentTypeId;
        this.masterFileId = input.masterFileId;
        this.masterFileAccountId = input.masterFileAccountId;
        this.dealerId = input.dealerId;
        this.leadId = input.leadId;
        this.customerId = input.customerId;
        this.title = input.title;
        this.description = input.description;
        this.url = input.url;
        this.allDay = input.allDay;
        this.startDateTime = input.startDateTime && AppointmentModel.getCorrectedDate(input.startDateTime.toString(), ptz.timeZoneOffsetMinutes) || null;
        this.endDateTime = input.endDateTime && AppointmentModel.getCorrectedDate(input.endDateTime.toString(), ptz.timeZoneOffsetMinutes) || null;
        this.lbStartDateTime = input.startDateTime && new Date(input.startDateTime.toString());
        this.lbEndDateTime = input.startDateTime && new Date(input.endDateTime.toString());
        this.reminders = input.reminders;
        this.isActive = input.isActive;
        this.modifiedBy = input.modifiedBy;
        this.modifiedOn = input.modifiedOn;
        this.createdBy = input.createdBy;
        this.createdOn = input.createdOn;
        this.ptz = ptz;
    }

    public static getCorrectedDate(dateStr: string, timeZoneOffsetMinutes: number): Date {
        //** Init */
        let result = new Date(dateStr);

        //** Convert */
        result.setTime(result.getTime() + (result.getTimezoneOffset() * 60000) + (timeZoneOffsetMinutes * 60000));

        //** Return result */
        return result;
    }

    public static getAppointmentClass(apptList: IAppointment[], ptz: IPoliticalTimeZoneResponseModel): AppointmentModel[] {
        //** Init */
        let result: AppointmentModel[] = [];

        //** Check there is a list */
        if (!apptList || apptList.length == 0) return result;

        //** Build list */
        apptList.forEach(appt => {
            result.push(new AppointmentModel(appt, ptz))
        });

        //** Return result */
        return result;
    }
}

