import { Injectable } from "@angular/core";
import { IDmeDealerSettings, IDmeDocumentTemplateModel } from "@TriInterfaces/dme/dme.root.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

@Injectable()
export class TriEsignApi extends TriBaseApi<IDmeDocumentTemplateModel> {

    constructor(private api: HttpService) {
        super();
    }


    getDmeMetaData(): Promise<IDmeDealerSettings> {
        return this.api.get(`Nuvol9Api/DocuSignDealerSettings/MetaData`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;
            //console.log(dto)

            this.checkResponse(dto as DtoModel<any>);

            // return dto.value;
            return JSON.parse(dto.value) as IDmeDealerSettings;
        })
    }

    createEnvelop(data: any): Promise<any>{
        return this.api.post(`Nuvol9Api/DocuSignEnvelops`, data).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }
    
}
