import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { MasterFileAccountCancelRequestModel } from "@TriInterfaces/ace/ace.aceRoot.interfaces";
import { IProductSkwSummaryResponseModel, ProductSkwFullModel } from "@TriInterfaces/ace/ace.productSkw.interfaces";
import { TriAceSearchApi } from "app/@core/backend/common/api/ace/ace.search.api";
import { TriAceWorkOrderApi } from "app/@core/backend/common/api/ace/aceWorkOrder.api";
import { TriAceRootApi } from "app/@core/backend/common/api/ace/triAceRoot.api";
import { TriMasterFileAccountApi } from "app/@core/backend/common/api/mas/triMasterFileAccount.api";
import { ProductSkwFullSearch } from "./aceSearch.actions";
import { GetCurrentWorkOrder, WorkOrderSend } from "./aceWorkOrder.actions";
import { GetAceMetadata, PostCancelAccount } from "./cancelAccount.actions";
import { GetAlarmProductList } from "./skwModal.actions";

export interface TriAceWorkOrderStateModel {
    productSkwSummary: IProductSkwSummaryResponseModel[],
}

const getInitialModel = (): TriAceWorkOrderStateModel => ({
    productSkwSummary: null
});

@Injectable()
@State<TriAceWorkOrderStateModel>({
    name: 'TriAceSearchRoot',
    defaults: getInitialModel(),
})
export class TriAceSearchState {
    constructor(private triAceSearchApi: TriAceSearchApi) { }

    @Selector()
    static getProductSkwFullSearch({ productSkwSummary }: TriAceWorkOrderStateModel) {
        return productSkwSummary;
    }

    @Action(ProductSkwFullSearch)
    productSkwFullSearch(
        ctx: StateContext<TriAceWorkOrderStateModel>,
        action: ProductSkwFullSearch
    ) {
        let request: any = action.payload;
        console.log("hello wolrd")
        this.triAceSearchApi.productSkwFullSearch(request).then((response) => {
            const state = ctx.getState();
            console.log('searching... ', response)

                let resultant = ctx.patchState({
                    ...state,
                    productSkwSummary: response,
                });

            return resultant;
        })
    }

}