import { Injectable } from "@angular/core";
import { TriCustomerSearchRequest, TriCustomerSearchResult } from "@TriInterfaces/ace/ace.customer-search-result.interfaces";
import { TriCustomerSearchDataAbstract } from "app/@core/interfaces/common/ace/triCustomer-search-data.abastract";
import { Observable } from "rxjs";
import { TriCustomerSearchApi } from "../../api/ace/triCustomer-search.api";

@Injectable()
export class TriCustomerSearchService extends TriCustomerSearchDataAbstract {

    constructor(private api: TriCustomerSearchApi) {
        super();
    }

    getData(payload: TriCustomerSearchRequest): Observable<TriCustomerSearchResult[]> {
        return this.api.smartTableSearch(payload);
    }


}