import { Injectable } from "@angular/core";
import { TriBaseApi } from "../_base.api";
import { GuiAccountTemplateTypesEnum, MacAccountWFStatusInterface } from '@TriInterfaces/mac/mac.account-wf.interfaces';
import { HttpService } from "../http.service";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";

@Injectable()
export class TriAccountWFApi extends TriBaseApi<MacAccountWFStatusInterface[]> {
    private readonly apiController: string = '';

    constructor(private api: HttpService) { 
        super();
    }

    public get(masterFileAccountId: string, accountTemplateTypeId: GuiAccountTemplateTypesEnum): Observable<MacAccountWFStatusInterface[]> {
        let params = `id=${masterFileAccountId}`;
        params = accountTemplateTypeId
            ? `${params}&tid=${accountTemplateTypeId}`
            : params;

        return this.api.get(`${this.apiController}/Nuvol9Api/MasAccountOnBoardStatus/Get?${params}`)
            .pipe(
                map(resposne => {
                    const dto = resposne as DtoModel<MacAccountWFStatusInterface[]>;

                    this.checkResponse(dto as DtoModel<any>);

                    return dto.value;
                })
            );
    }
}