import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { TriAccountApi } from "app/@core/backend/common/api/mas/triAccount.api";
import { LeadCreditPullResponseModel } from "@TriInterfaces/qal/qal.lead.interfaces";
import { GetLeadRespModelAction } from "./lead.actions";
import { TriLeadApi } from "app/@core/backend/common/api/qal/triLead.api";

export interface TriLeadStateModel {
    defaultLeadCrePullModel: LeadCreditPullResponseModel
}

const getInitialModel = (): TriLeadStateModel => ({
    defaultLeadCrePullModel: null
});

@Injectable()
@State<TriLeadStateModel>({
    name: 'TriLeadRoot',
    defaults: getInitialModel(),
})
export class TriLeadState {
    constructor(private triLeadApi: TriLeadApi) { }

    @Selector()
    static getDefaultLeadCrePullModelData({ defaultLeadCrePullModel }: TriLeadStateModel) {
        return defaultLeadCrePullModel;
    }

    @Action(GetLeadRespModelAction)
    getDefaultLeadCrePullModel(
        ctx: StateContext<TriLeadStateModel>,
        action: GetLeadRespModelAction
    ) {
        const dealerId: number = action.dealerId;
        const seasonId: number = action.seasonId;
        //console.log("getting with", dealerId, seasonId)
        this.triLeadApi.refreshLeadRespModel(dealerId, seasonId).then((response) => {
            const state = ctx.getState();
            let resultant = ctx.patchState({
                ...state,
                defaultLeadCrePullModel: response,
            });
            return resultant;
        })
    }
}