import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { HttpService } from "../http.service";

import { DtoModel } from '@TriInterfaces/root-dto.interfaces';
import { TriCustomerSearchRequest, TriCustomerSearchResult } from '@TriInterfaces/ace/ace.customer-search-result.interfaces';
import { map } from "rxjs/operators";
import { TriBaseApi } from "../_base.api";

@Injectable()
export class TriCustomerSearchApi extends TriBaseApi<TriCustomerSearchResult> {
    private readonly apiController: string = 'Nuvol9Api/Search';

    constructor(private api: HttpService) {
        super();
    }

    smartTableSearch(payload: TriCustomerSearchRequest): Observable<TriCustomerSearchResult[]> {
        return this.api.post(`${this.apiController}/SmartQuery`, payload)
            .pipe(
                map((response) => {
                    const dto = response as DtoModel<TriCustomerSearchResult[]>;

                    this.checkResponse(dto as DtoModel<any>);

                    return dto.value;
                })
            );
    }
}
