import { Injectable } from "@angular/core";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { IMasEquipmentWithMetaData, MasEquipmentItem } from "@TriInterfaces/mas/mas.equipment.interfaces";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { EquipmentRootMetaDataModel } from "@TriInterfaces/mas/mas.root.interfaces";

@Injectable()
export class TriAccountEquipmentApi extends TriBaseApi<any>{

    constructor(private api: HttpService) {
        super();
    }

    getEquipmentBySkw(skw: string): Promise<IMasEquipmentWithMetaData> {
        return this.api.get(`Nuvol9Api/MasAccountEquipment/BySkw/?id=${skw}`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value) as IMasEquipmentWithMetaData;
        })
    }

     getEquipmentMetaDataResources(): Promise<EquipmentRootMetaDataModel> {
		let uri = `Nuvol9Api/MasEquipment/GetMetaData`;
        return this.api.get(uri).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            console.log("meta api", dto.value);

            return dto.value;
        })
    }

    
    getEquipmentByBarcode(barcode: string, bypass?: boolean): Promise<IMasEquipmentWithMetaData[]> {
        let uri = bypass
            ? `Nuvol9Api/MasAccountEquipment/ByBarcode/?id=${barcode}&bypass=${bypass}`
            : `Nuvol9Api/MasAccountEquipment/ByBarcode/?id=${barcode}`;
        return this.api.get(uri).toPromise().then((data) => {

            const dto = data as DtoModel<any>; 

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value) as IMasEquipmentWithMetaData[];
        })
    }

    getEquipment(masAccountId: number):Promise<MasEquipmentItem[]> {
        return this.api.get(`Nuvol9Api/MasAccountEquipment/?id=${masAccountId}`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value) as MasEquipmentItem[];
        })
    }

    save(request: any): Promise<any> {
        let data: any = { requestJsonObject: JSON.stringify(request) };
        if(request.id){
            return this.api.put(`Nuvol9Api/MasAccountEquipment/`, data).toPromise().then((data) => {
                return data;
            })
        }else{
            return this.api.post(`Nuvol9Api/MasAccountEquipment/`, data).toPromise().then((data) => {
                return data;
            })
        }
        
    }


    delete(id: number, mfaId: string){
        return this.api.delete(`Nuvol9Api/MasAccountEquipment/${id}/${mfaId}`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value.responseJsonObject);
        })
    }
}