import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { SettingsApi } from '../api/settings.api';
import { SettingsData, Settings } from 'app/@core/interfaces/common/settings';
import { UserSessionInfoModel, SecurityLevelEnum } from '@TriInterfaces/user-session-info.interfaces';

@Injectable()
export class SettingsService extends SettingsData {
	constructor(private api: SettingsApi) {
		super();
	}

	getCurrentSetting(): Observable<Settings> {
		return this.api.getCurrent();
	}

	updateCurrent(setting: any): Observable<Settings> {
		return this.api.updateCurrent(setting);
	}

	public getUserFromLocalStorage(): UserSessionInfoModel {
		let result = localStorage.getItem("auth-user");
		return JSON.parse(result);
	}

	public hasRoleOrHigher(roleLevelEnum: SecurityLevelEnum): Boolean {
		let hasRole: boolean = false;
		let sessionInfo: UserSessionInfoModel = this.getUserFromLocalStorage();
		if (sessionInfo != null) {
			sessionInfo.userData.roles.forEach(role => {
				if (roleLevelEnum >= role.securityLevel) {
					hasRole = true;
				}
			});
		} else {
			// FIX: REMOVE once local storage auth is fully implemented.
			hasRole = true;
        }

		return hasRole;
	}
}
