import { Pipe, PipeTransform } from '@angular/core';
/*
* Filters payments on the paymentsList screen.
*/
@Pipe({
	name: 'payFilter',
	pure: false
})
export class PayFilterPipe implements PipeTransform {
	transform(items: any[], args: any[]): any {
		// filter items array, items which match and return true will be kept, false will be filtered out
		return items.filter((item) => {
			if (!args) return true;

			for (var i = 0; i < args.length; i++) {
				if (item.resultCode.indexOf(args[0].resultCode) !== -1) {
					return true;
				}
			}
			// return item.resultCode.indexOf(args[0].resultCode) !== -1
			return false;
		});
	}
}

@Pipe({
	name: 'payGoodFilter',
	pure: false
})
export class PayGoodFilterPipe implements PipeTransform {
	transform(items: any[], args: any[]): any {
		// filter items array, items which match and return true will be kept, false will be filtered out
		return items.filter((item) => {
			if (item.resultCode.indexOf('Ok') === -1) return false;
			// if (item.paymentId <= 0) return false;
			if (item.amountLeft <= 0) return false;

			return true;
		});
	}
}

@Pipe({
	name: 'paymentStatus',
	pure: false
})
export class PaymentStatusPipe implements PipeTransform {
	transform(paymentId: number, amountLeft: number, resultCode: string): string {
		if (resultCode != 'Ok') return 'N/A';

		if (paymentId > 0) {
			return amountLeft > 0
				? "Available"
				: "Consumed";
		}

		// ** Default response.
		return "Unbound";
	}
}