import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";

import { TriCustomerTicketInterface } from '@TriInterfaces/sce/sce.tickets.interfaces';
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";

@Injectable()
export class TriCustomerTicketsApi extends TriBaseApi<TriCustomerTicketInterface> {
    private readonly apiController: string = 'Nuvol9Api/MasterFileAccount/';

    constructor(private api: HttpService) {
        super();
    }

    getTicketsByMasterFileId(masterFileId: number): Observable<TriCustomerTicketInterface[]> {
        return this.api.get(`${this.apiController}/${masterFileId}/Tickets`)
            .pipe(
                map(response => {
                    const dto = response as DtoModel<TriCustomerTicketInterface[]>;

                    this.checkResponse(dto as DtoModel<any>);

                    return dto.value;
                })
            );
    }

}