import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { IMasEquipmentWithMetaData } from "@TriInterfaces/mas/mas.equipment.interfaces";
import { TriAccountEquipmentApi } from "app/@core/backend/common/api/mas/triAccountEquipment.api";
import { GetBarcodeDialogAction } from "./tri-add-barcode-dialog.action";

export interface TriBarcodeStateModel {
    barcode: IMasEquipmentWithMetaData[];
    
}

const getInitialModel = (): TriBarcodeStateModel => ({
    barcode: []
});

@Injectable()
@State<TriBarcodeStateModel>({
    name: 'TriBarcodeRoot',
    defaults: getInitialModel(),
})
export class TriBarcodeState {
    constructor(private triBarcode: TriAccountEquipmentApi) { }

    @Selector()
    static getBarcode({ barcode }: TriBarcodeStateModel) {
        return barcode;
    }

    @Action(GetBarcodeDialogAction)
    getBarcodeAction(
        ctx: StateContext<TriBarcodeStateModel>,
        action: GetBarcodeDialogAction
    ) {
        const barcode = action.barcode;
        const bypass = action.bypass;
        this.triBarcode.getEquipmentByBarcode(barcode, bypass).then((response) => {
            const state = ctx.getState();
            let resultant = ctx.patchState({
                ...state,
                barcode: response,
            });
            return resultant;
        })
    }

}