import { Injectable } from "@angular/core";
import { IInvoicePayments, IInvoicePaySummary } from "@TriInterfaces/ace/ace.invoice.interfaces";
import { TriCustomerPaymentItemInterface } from "@TriInterfaces/ace/ace.customer-root.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

@Injectable()
export class TriAceWorkOrderApi extends TriBaseApi<TriCustomerPaymentItemInterface> {

    constructor(private api: HttpService) {
        super();
    }

    getCurrentWorkOrder(mfaId: string): Promise<any[]> {
        return this.api.get(`Nuvol9Api/MasterFileAccount/${mfaId}/CurrentWorkOrder`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value) as any;
        })
    }

    workOrderSend(payload: any): Promise<any> {
        let data = {
            jsonInput: JSON.stringify(payload)
        };
        return this.api.post(`Nuvol9Api/WorkOrder/Send/`, data).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            return dto.value;
        })
    }

    workOrderItemSave(payload: any): Promise<any> {
        let data: any = {
            "jsonInput": JSON.stringify(payload)
        };
        return this.api.post(`Nuvol9Api/WorkOrderItem`, data).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            return dto.value;
        })
    }

}