import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { GetCustomerBillingSummaryAction, GetCustomerInvoiceHistoryAction, GetCustomerRoutingBankInfoAction } from "./triPaymentSummary.actions";
import { TriCustomerPaymentInvoiceApi } from "app/@core/backend/common/api/ace/triCustomerPaymentInvoice.api";
import { TriCustomerBillingSummaryInterface } from "@TriInterfaces/ace/ace.customer-root.interfaces";

export interface TriPaymentSummaryModel {
    customerBillingSummary: TriCustomerBillingSummaryInterface[],
    customerInvoiceHistory: any[],
    customerRoutingBankInfo: any[]
}

const getInitialModel = (): TriPaymentSummaryModel => ({
    customerBillingSummary: [],
    customerInvoiceHistory: [],
    customerRoutingBankInfo: []
});

@Injectable()
@State<TriPaymentSummaryModel>({
    name: 'TriPaymentSummaryRoot',
    defaults: getInitialModel(),
})
export class TriPaymentSummaryState {
    constructor(private triCustomerPaymentInvoiceApi: TriCustomerPaymentInvoiceApi) { }

    @Selector()
    static getCustomerBillingSummaryData({ customerBillingSummary }: TriPaymentSummaryModel) {
        return customerBillingSummary;
    }

    @Selector()
    static getCustomerInvoiceHistoryData({ customerInvoiceHistory }: TriPaymentSummaryModel) {
        return customerInvoiceHistory;
    }

    @Selector()
    static getCustomerRoutingBankInfo({ customerRoutingBankInfo }: any) {
        return customerRoutingBankInfo;
    }

    @Action(GetCustomerBillingSummaryAction)
    getCustomerBillingSummaryAction(
        ctx: StateContext<TriPaymentSummaryModel>,
        action: GetCustomerBillingSummaryAction
    ) {
        const id: number = action.id;
    
        this.triCustomerPaymentInvoiceApi.getCustomerBillingSummary(id).then((response) => {
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                customerBillingSummary: response,
            });
            return resultant;
        })
    }

    @Action(GetCustomerInvoiceHistoryAction)
    getCustomerInvoiceHistoryAction(
        ctx: StateContext<TriPaymentSummaryModel>,
        action: GetCustomerInvoiceHistoryAction
    ) {
        const id: number = action.id;

        this.triCustomerPaymentInvoiceApi.getCustomerInvoiceHistory(id).then((response) => {
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                customerInvoiceHistory: response,
            });
            return resultant;
        })
    }

    @Action(GetCustomerRoutingBankInfoAction)
    getCustomerRoutingBankInfoAction(
        ctx: StateContext<TriPaymentSummaryModel>,
        action: GetCustomerRoutingBankInfoAction
    ) {
        const rid: string = action.rid;

        this.triCustomerPaymentInvoiceApi.getRoutingBankInfo(rid).then((response) => {
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                customerRoutingBankInfo: response,
            });
            return resultant;
        })
    }
}