import { Injectable } from "@angular/core";
import { TriMetaDataInterface } from "@TriInterfaces/gui/gui.metadata.interfaces";
import { IInventoryStockItem, IResourceItemResponseModel } from "@TriInterfaces/ine/ineRoot.interfaces";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";

@Injectable()
export class TriIneRootApi extends TriBaseApi<TriMetaDataInterface> {

    constructor(private api: HttpService) {
        super();
    }

    getResourcesByLocationTypeId(id: string): Promise<IResourceItemResponseModel[]> {
        return this.api.get(`Nuvol9Api/LocationType/Resources/?id=${id}`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            console.log(dto)
            this.checkResponse(dto as DtoModel<any>);
            return dto.value;
        })
    }


    getInventoryByUniqueIdAndLocationType(id: string, locationTypeId: string): Promise<IInventoryStockItem[]> {
        return this.api.get(`Nuvol9Api/ProductBarcodeTracking/Inventory/${id}/${locationTypeId}`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            return JSON.parse(dto.value) as IInventoryStockItem[];
        })
    }

    
}