import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { IInvoicePayments } from "@TriInterfaces/ace/ace.invoice.interfaces";
import { RptDealerAgingContractItemFullModel } from "@TriInterfaces/rpt/rpt.rptRoot.interfaces";
import { TriCustomerInvoicePaymentsApi } from "app/@core/backend/common/api/ace/triCustomerInvoicePayments.api";
import { TriRptRootApi } from "app/@core/backend/common/api/rpt/rptRoot.api";
import { GetAgingDealerReport, GetOrderByInvoiceIdAction } from "./rpt.action";

export interface TriInvoiceStateModel {
    agingDealerRepot: RptDealerAgingContractItemFullModel[],
    orders: any
}

const getInitialModel = (): TriInvoiceStateModel => ({
    agingDealerRepot: [],
    orders: null
});

@Injectable()
@State<TriInvoiceStateModel>({
    name: 'TriRptRoot',
    defaults: getInitialModel(),
})
export class TriRptRootState {
    constructor(private triRptRootApi: TriRptRootApi) { }

    @Selector()
    static getAgingDealerReportData({ agingDealerRepot }: TriInvoiceStateModel) {
        return agingDealerRepot;
    }

    @Selector()
    static getOrderByInvoiceIdData({ orders }: TriInvoiceStateModel) {
        return orders;
    }

    @Action(GetAgingDealerReport)
    getAgingDealerReport(
        ctx: StateContext<TriInvoiceStateModel>,
        action: GetAgingDealerReport
    ) {
        const inputData: number = action.payload;
        
        this.triRptRootApi.getAgingDealerReport(inputData).then((response) => {
            const state = ctx.getState(); 

            let resultant = ctx.patchState({
                ...state,
                agingDealerRepot: response,
            });

            return resultant;
        })
    }

    @Action(GetOrderByInvoiceIdAction)
    getOrderByInvoiceId(
        ctx: StateContext<TriInvoiceStateModel>,
        action: GetOrderByInvoiceIdAction
    ) {
        const inputData: number = action.id;
        
        this.triRptRootApi.getOrderByInvoiceId(inputData).then((response) => {
            const state = ctx.getState(); 

            let resultant = ctx.patchState({
                ...state,
                orders: response,
            });

            return resultant;
        })
    }
}