import { Injectable } from "@angular/core";
import { LeadCreditPullResponseModel, LeadRequestModel } from "@TriInterfaces/qal/qal.lead.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

@Injectable()
export class TriLeadApi extends TriBaseApi<LeadRequestModel> {

    constructor(private api: HttpService) {
        super();
    }

    refreshLeadRespModel(dealerId: number, seasonId: number): Promise<LeadCreditPullResponseModel> {
        return this.api.get(`Nuvol9Api/LeadCreditPull/GetDefaultLeadPullCreditResponse?dealerId=${dealerId}&seasonId=${seasonId}`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            this.checkResponse(dto as DtoModel<any>);
            // return dto.value;
            return dto.value;
        })
    }
}