import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { MasterFileAccountCancelRequestModel } from "@TriInterfaces/ace/ace.aceRoot.interfaces";
import { ProductSkwFullModel } from "@TriInterfaces/ace/ace.productSkw.interfaces";
import { TriAceWorkOrderApi } from "app/@core/backend/common/api/ace/aceWorkOrder.api";
import { TriAceRootApi } from "app/@core/backend/common/api/ace/triAceRoot.api";
import { TriMasterFileAccountApi } from "app/@core/backend/common/api/mas/triMasterFileAccount.api";
import { GetCurrentWorkOrder, WorkOrderItemSave, WorkOrderSend } from "./aceWorkOrder.actions";
import { GetAceMetadata, PostCancelAccount } from "./cancelAccount.actions";
import { GetAlarmProductList } from "./skwModal.actions";

export interface TriAceWorkOrderStateModel {
    currentWorkOrder: any,
}

const getInitialModel = (): TriAceWorkOrderStateModel => ({
    currentWorkOrder: null
});

@Injectable()
@State<TriAceWorkOrderStateModel>({
    name: 'TriAceWorkOrderRoot',
    defaults: getInitialModel(),
})
export class TriAceWorkOrderState {
    constructor(private triAceWorkOrderApi: TriAceWorkOrderApi) { }

    @Selector()
    static getAceMetadataData({ currentWorkOrder }: TriAceWorkOrderStateModel) {
        return currentWorkOrder;
    }


    @Action(GetCurrentWorkOrder)
    getAceMetadata(
        ctx: StateContext<TriAceWorkOrderStateModel>,
        action: GetCurrentWorkOrder
    ) {
        const payload: string = action.mfaId;

        this.triAceWorkOrderApi.getCurrentWorkOrder(payload).then(
            (response: any) => {
                const state = ctx.getState();

                let resultant = ctx.patchState({
                    ...state,
                    currentWorkOrder: response,
                });

                return resultant;
            }
        );

    }


    @Action(WorkOrderSend)
    sendWorkOrder(
        ctx: StateContext<TriAceWorkOrderStateModel>,
        action: WorkOrderSend
    ) {
        let request: any = action.payload;

        this.triAceWorkOrderApi.workOrderSend(request).then((response) => {
            // TODO: update state?
            return;
        })
    }

    @Action(WorkOrderItemSave)
    workOrderItemSave(
        ctx: StateContext<TriAceWorkOrderStateModel>,
        action: WorkOrderItemSave
    ) {
        let request: any = action.payload;

        this.triAceWorkOrderApi.workOrderItemSave(request).then((response) => {
            // TODO: update state?
            return;
        })
    }

}