import { Injectable } from "@angular/core";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { IndustryNumber } from "@TriInterfaces/mas/mas.industry-number.interfaces";
import { MasAccountMetaDataModel } from "@TriInterfaces/mas/mas.root.interfaces";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { AccountTemplate, MasAccountModel } from "@TriInterfaces/mas/mas.account.interfaces";

@Injectable()
export class TriAccountApi extends TriBaseApi<any>{

    constructor(private api: HttpService) {
        super();
    }

    getIndustryNumbers(masterFileAccountId: string): Promise<IndustryNumber[]> {
        return this.api.get(`Nuvol9Api/CentralStation/GetIndustryNumbers?id=${masterFileAccountId}`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            // return dto.value;
            return dto.value;
        })
    }

    getAccountTemplates(dealerId: number): Promise<AccountTemplate[]>{
        return this.api.get(`Nuvol9Api/MasAccountTemplate/GetByDealerId?id=${dealerId}`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }

    save(data: MasAccountModel): Promise<any>{
        return this.api.post(`Nuvol9Api/MasAccount/Save`, data).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }

    saveSettings(data: any): Promise<any>{
        return this.api.post(`Nuvol9Api/MasAccount/SaveSettings`, data).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }

    getMasAccountMetaData(): Promise<MasAccountMetaDataModel> {
        return this.api.get(`Nuvol9Api/MasAccount/MetaData`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value) as MasAccountMetaDataModel;
        })
    }

    issueIndustryNumber(payload: any): Promise<any> {
        return this.api.post(`Nuvol9Api/CentralStation/IssueIndustryNumber`, payload).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }
}