import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { TriAccountApi } from "app/@core/backend/common/api/mas/triAccount.api";
import { LeadCreditPullResponseModel } from "@TriInterfaces/qal/qal.lead.interfaces";
import { GetLeadRespModelAction } from "./lead.actions";
import { TriLeadApi } from "app/@core/backend/common/api/qal/triLead.api";
import { MasterFileDispositionApi } from "app/@core/backend/common/api/qal/masterFileDisposition.api";
import { GetMasterFileDisposition } from "./disposition.actions";

export interface TriQalMasterFileDispositionsModel {
    masterFileDisposition: any
}

const getInitialModel = (): TriQalMasterFileDispositionsModel => ({
    masterFileDisposition: null
});

@Injectable()
@State<TriQalMasterFileDispositionsModel>({
    name: 'TriQalMasterFileDisposition',
    defaults: getInitialModel(),
})
export class TriQalMasterFileDispositionsState {
    constructor(private masterFileDispositionApi: MasterFileDispositionApi) { }

    @Selector()
    static getMasterFileDispositionData({ masterFileDisposition }: TriQalMasterFileDispositionsModel) {
        return masterFileDisposition;
    }

    @Action(GetMasterFileDisposition)
    getDefaultLeadCrePullModel(
        ctx: StateContext<TriQalMasterFileDispositionsModel>,
        action: GetMasterFileDisposition
    ) {
        const id: number = action.id;
        const mfaId: string = action.mfaId;

        this.masterFileDispositionApi.get(id, mfaId).then((response) => {
            const state = ctx.getState();
            let resultant = ctx.patchState({
                ...state,
                masterFileDisposition: response,
            });
            return resultant;
        })
    }
}