import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

import { RptDealerAgingContractItemFullModel } from '@TriInterfaces/rpt/rpt.rptRoot.interfaces';
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";

@Injectable()
export class TriRptRootApi extends TriBaseApi<any> {

    constructor(private api: HttpService) {
        super();
    }

    getAgingDealerReport(id?: number): Promise<RptDealerAgingContractItemFullModel[]> {
        let uri = id ? `Nuvol9Api/Aging/${id}` : 'Nuvol9Api/Aging';
        return this.api.get(uri).toPromise().then((data) => {
            const dto = data as DtoModel<RptDealerAgingContractItemFullModel[]>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }

    getOrderByInvoiceId(id: number): Promise<any> {
        return this.api.get(`Nuvol9Api/CustomerOrders?id=${id}`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value.dataLoad);
        })
    }

}