import { DtoMessageCodes, DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { throwError } from "rxjs";

export class TriBaseApi<T> {
    
    checkResponse(dtoEnvelop: DtoModel<T>): T {
        if (dtoEnvelop.code !== DtoMessageCodes.Success) throwError(`DtoMessage: ${dtoEnvelop.dtoMessage}`);

        return dtoEnvelop.value;
    }
}