import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { CustomerContractSummaryModel } from "@TriInterfaces/rbe/rbe.root.interfaces";
import { CreateCustomerContractSkipPeriod, DeleteCustomerContractSkipPeriod, GetCustomerContracts, GetCustomerContractSteps } from "./rbeRoot.actions";
import { TriRbeRootApi } from "app/@core/backend/common/api/rbe/rbeRoot.api";
import { ContractStepsResposne } from "@TriInterfaces/ace/ace.aceRoot.interfaces";

export interface TriRbeRootModel {
    customerContracts: CustomerContractSummaryModel[],
    customerContractSteps:ContractStepsResposne[]
}

const getInitialModel = (): TriRbeRootModel => ({
    customerContracts: [],
    customerContractSteps: []
});

@Injectable()
@State<TriRbeRootModel>({
    name: 'TriRbeStateRoot',
    defaults: getInitialModel(),
})
export class TriRbeRootState {
    constructor(private triRbeRootApi: TriRbeRootApi) { }

    @Selector()
    static getCustomerContractsData({ customerContracts }: TriRbeRootModel) {
        return customerContracts;
    }

    @Selector()
    static getCustomerContractsStepsData({ customerContractSteps }: TriRbeRootModel) {
        return customerContractSteps;
    }

    @Action(GetCustomerContracts)
    getCustomerContracts(
        ctx: StateContext<TriRbeRootModel>,
        action: GetCustomerContracts
    ) {
        const id : number = action.id;
        const mfaId : string = action.mfaId;
    
        this.triRbeRootApi.getCustomerContracts(id, mfaId).then((response) => {
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                customerContracts: response,
            });
            return resultant;
        })
    }

    @Action(GetCustomerContractSteps)
    getCustomerContractSteps(
        ctx: StateContext<TriRbeRootModel>,
        action: GetCustomerContractSteps
    ) {
        const id : string = action.id;
    
        this.triRbeRootApi.getCustomerContractSteps(id).then((response) => {
            const state = ctx.getState();
            console.log("triRbeRoot 1", response)

            let resultant = ctx.patchState({
                ...state,
                customerContractSteps: response,
            });
            return resultant;
        })
    }

    @Action(CreateCustomerContractSkipPeriod)
    createCustomerContractSkipPeriod(
        ctx: StateContext<TriRbeRootModel>,
        action: CreateCustomerContractSkipPeriod
    ) {
        const data : any = action.data;
    
        this.triRbeRootApi.createCustomerContractSkipPeriod(data).then((response) => {
            return;
        })
    }


    @Action(DeleteCustomerContractSkipPeriod)
    deleteCustomerContractSkipPeriod(
        ctx: StateContext<TriRbeRootModel>,
        action: DeleteCustomerContractSkipPeriod
    ) {
        const data : string = action.id;
    
        this.triRbeRootApi.deleteCustomerContractSkipPeriod(data).then((response) => {
            return;
        })
    }
}