import { Injectable } from "@angular/core";
import { LeadRequestModel } from "@TriInterfaces/qal/qal.lead.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

@Injectable()
export class TriPrincipleApi extends TriBaseApi<LeadRequestModel> {

    constructor(private api: HttpService) {
        super();
    }

    saveverfyLeadManualOveride(save: LeadRequestModel): Promise<LeadRequestModel> {
        return this.api.post(`Nuvol9APi/LeadCreditPull/SaveManual`, save).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            console.log(dto)
            this.checkResponse(dto as DtoModel<any>);
            // return dto.value;
            return dto.value;
        })
    }
    verfyLead(save: LeadRequestModel): Promise<LeadRequestModel>{
        return this.api.post(`Nuvol9APi/LeadCreditPull/Verify`, save).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            console.log(dto)
            this.checkResponse(dto as DtoModel<any>);
            // return dto.value;
            return dto.value;
        })
    }
}