import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext } from "@ngxs/store";
import { TriAddressResponseInterface, TriVerifyAddressRequestInterface } from "@TriInterfaces/qal/qal.address.interfaces";
import { TriAddressVerificationApi } from "app/@core/backend/common/api/qal/address-verification.api";
import { map, tap } from "rxjs/operators";
import { IInvoicePayments, IInvoiceRequestModel } from "@TriInterfaces/ace/ace.invoice.interfaces";
import { CreateInvoiceAction, FindOpenInvoiceAction, GetInvoiceAction, GetInvoiceHeaderAction, GetInvoiceMetaDataAction } from "./invoice.action";
import { TriCustomerInvoicePaymentsApi } from "app/@core/backend/common/api/ace/triCustomerInvoicePayments.api";
import { TriInvoiceApi } from "app/@core/backend/common/api/rpt/invoice.api";
import { IRptFullfilmentInvoiceHeaderModel } from "@TriInterfaces/rpt/tri-IRptFullfilement.interfaces";

export interface TriInvoiceStateModel {
    invoicePayments: IInvoicePayments[],
    invoiceHeader: any,
    openInvoices: IInvoiceRequestModel[],
    invoiceMetaData: any
}

const getInitialModel = (): TriInvoiceStateModel => ({
    invoicePayments: [],
    invoiceHeader: null,
    openInvoices: [],
    invoiceMetaData: null
});

@Injectable()
@State<TriInvoiceStateModel>({
    name: 'TriPaymentRoot',
    defaults: getInitialModel(),
})
export class TriInvoiceState {
    constructor(private triCustomerInvoicePaymentsApi: TriCustomerInvoicePaymentsApi, private triInvoiceApi: TriInvoiceApi) { }

    @Selector()
    static getPaymentsForInvoice({ invoicePayments }: TriInvoiceStateModel) {
        return invoicePayments;
    }

    @Selector()
    static getFindOpenInvoice({ openInvoices }: TriInvoiceStateModel) {
        return openInvoices;
    }

    @Selector()
    static getInvoiceHeaderData({ invoiceHeader }: TriInvoiceStateModel) { 
        return invoiceHeader;
    }

    @Action(CreateInvoiceAction)
    masInvoiceCreate(
        ctx: StateContext<TriInvoiceStateModel>,
        action: CreateInvoiceAction
    ) {
        const inputData: any = action.payload;
        
        this.triCustomerInvoicePaymentsApi.masInvoiceCreate(inputData).then((response) => {
            //console.log("received response", response)
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                openInvoices: response,
            });

            return resultant;
        })
    }



    @Action(GetInvoiceAction)
    getInvoiceById(
        ctx: StateContext<TriInvoiceStateModel>,
        action: GetInvoiceAction
    ) {
        const inputData: number = action.payload;
        
        this.triCustomerInvoicePaymentsApi.getPaymentsForInvoice(inputData).then((response) => {
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                invoicePayments: response,
            });

            return resultant;
        })
    }


    @Action(FindOpenInvoiceAction)
    findOpenInvoice(
        ctx: StateContext<TriInvoiceStateModel>,
        action: FindOpenInvoiceAction
    ) {
        const mfaId: string = action.mfaId;
        const schemaId: string = action.schemaId;
        
        this.triCustomerInvoicePaymentsApi.findOpenInvoice(schemaId, mfaId).then((response) => {
            const state = ctx.getState();
            //console.log("gotten",response)

            let resultant = ctx.patchState({
                ...state,
                openInvoices: response,
            });
            return resultant;
        })
    }


    @Action(GetInvoiceHeaderAction)
    getInvoiceHeader(
        ctx: StateContext<TriInvoiceStateModel>,
        action: GetInvoiceHeaderAction
    ) {
        const inputData: number = action.payload;
        
        this.triInvoiceApi.getInvoiceHeader(inputData).then((response) => {
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                invoiceHeader: response,
            });
            return resultant;
        })
    }

    @Action(GetInvoiceMetaDataAction)
    getInvoiceMetaData(
        ctx: StateContext<TriInvoiceStateModel>,
        action: GetInvoiceMetaDataAction
    ) {
        
        this.triInvoiceApi.getInvoiceMetaData().then((response) => {
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                invoiceMetaData: response,
            });
            return resultant;
        })
    }
}