import { Injectable } from "@angular/core";
import { IInvoicePayments, IInvoicePaySummary, IInvoiceRequestModel } from "@TriInterfaces/ace/ace.invoice.interfaces";
import { TriCustomerPaymentItemInterface } from "@TriInterfaces/ace/ace.customer-root.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

@Injectable()
export class TriCustomerInvoicePaymentsApi extends TriBaseApi<TriCustomerPaymentItemInterface> {
    private readonly apiController: string = 'Nuvol9Api/Invoice';

    constructor(private api: HttpService) {
        super();
    }

    public masInvoiceCreate(data: IInvoiceRequestModel): Promise<any> {
        return this.api.post(this.apiController, data).toPromise().then((data) => {
            
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }

    public findOpenInvoice(schemaId: string, mfaId: string): Promise<any> {
        return this.api.get(`${this.apiController}/BySchema/?id=${schemaId}&mfaId=${mfaId}`).toPromise().then((data) => {
            
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }


    getPaymentsForInvoice(id: number): Promise<IInvoicePayments[]> {

        return this.api.get(`${this.apiController}/${id}/Payments`).toPromise().then((data) => {
            //console.log("done", data);
            
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value.jsonOutPutMethod) as IInvoicePayments[]
        })
    }

    getPaySummaryForInvoice(id: number): Promise<IInvoicePaySummary[]> {
        return this.api.get(`${this.apiController}/${id}/Payments`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return JSON.parse(dto.value.jsonOutPutMethod2) as IInvoicePaySummary[]
        })
    }
}