import { Injectable } from "@angular/core";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { IProductSkwSummaryResponseModel, ProductSkwFullModel } from "@TriInterfaces/ace/ace.productSkw.interfaces";
import { AceRootMetaDataModel } from "@TriInterfaces/ace/ace.aceRoot.interfaces";

@Injectable()
export class TriAceSearchApi extends TriBaseApi<any> {

    constructor(private api: HttpService) {
        super();
    }


    productSkwFullSearch(data: any): Promise<IProductSkwSummaryResponseModel[]> {
        return this.api.post(`Nuvol9Api/ProductSkw/FullSearch`, data).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
           
            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })      
    }


}