import { Injectable } from "@angular/core";
import { MasterFileAccountCancelRequestModel } from "@TriInterfaces/ace/ace.aceRoot.interfaces";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";

@Injectable()
export class TriMasterFileAccountApi extends TriBaseApi<any>{

    constructor(private api: HttpService) {
        super();
    }


    cancelAccount(data: MasterFileAccountCancelRequestModel): Promise<any> {
        return this.api.post(`Nuvol9Api/MasterFileAccount/Cancel`, data).toPromise().then((data) => {
            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
    }
}