import { Injectable } from "@angular/core";
import { Observable } from "rxjs";

import { TriCustomerCardRootInterface } from '@TriInterfaces/ace/ace.customer-root.interfaces';
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { map } from "rxjs/operators";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";

@Injectable()
export class TriCustomerApi extends TriBaseApi<TriCustomerCardRootInterface> {
    private readonly apiController: string = 'Nuvol9Api/TriCustomer';

    constructor(private api: HttpService) {
        super();
    }

    getCustomerCard(id: number): Observable<TriCustomerCardRootInterface> {
        return this.api.get(`${this.apiController}/${id}`)
            .pipe(
                map((resposne, ndx) => {
                    const dto = resposne as DtoModel<TriCustomerCardRootInterface>;

                    this.checkResponse(dto as DtoModel<any>);

                    return dto.value;
                })
            );
    }

    getMasterCustomerByCmfid(cmfid:number, dealerId:number): Promise<TriCustomerCardRootInterface> {
        return this.api.get(`Nuvol9Api/MasterFile/Build?id=${cmfid}&dealerId=${dealerId}`).toPromise().then((result) => {
            // TODO: change DtoModel<any> to correct type
            const dto = result as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            return dto.value;
        })
           
    }
}