import { Injectable } from "@angular/core";
import { Action, Selector, State, StateContext, Store } from "@ngxs/store";
import { IInventoryStockItem, IResourceItemResponseModel } from "@TriInterfaces/ine/ineRoot.interfaces";
import { TriGuiMetaDataApi } from "app/@core/backend/common/api/gui/triMetadata.api";
import { TriIneRootApi } from "app/@core/backend/common/api/ine/triIneRoot.api";
import { tap } from "rxjs/operators";
import { GetResourcesByLocationTypeId, GetResourcesInventory } from "./ineRoot.actions";

export class TriIneRootStateModel {
    resourceList: IResourceItemResponseModel[];
    resourcesInventory:  IInventoryStockItem[];
}

@Injectable()
@State<TriIneRootStateModel>({
    name: 'TriIneRoot',
    defaults: {
        resourceList: null,
        resourcesInventory: null
    }
})
export class TriIneState {

    @Selector()
    static getResourcesByLocationTypeIdData({ resourceList }: TriIneRootStateModel) {
        return resourceList;
    }

    @Selector()
    static getResourcesInventoryData({ resourcesInventory }: TriIneRootStateModel) {
        return resourcesInventory;
    }

    constructor(private triIneRootApi: TriIneRootApi) {
    }


    @Action(GetResourcesByLocationTypeId)
    getResourcesByLocationTypeId(
        ctx: StateContext<TriIneRootStateModel>,
        action: GetResourcesByLocationTypeId
    ) {
        const payload: string = action.payload;
        
        this.triIneRootApi.getResourcesByLocationTypeId(payload).then((response) => {
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                resourceList: response,
            });
            return resultant;
        })
    }

    @Action(GetResourcesInventory)
    getResourcesInventory(
        ctx: StateContext<TriIneRootStateModel>,
        action: GetResourcesInventory
    ) {
        const id: string = action.id;
        const ltid: string = action.locationTypeId;
        
        this.triIneRootApi.getInventoryByUniqueIdAndLocationType(id, ltid).then((response) => {
            const state = ctx.getState();

            let resultant = ctx.patchState({
                ...state,
                resourcesInventory: response,
            });
            return resultant;
        })
    }

}