import { Injectable } from "@angular/core";

import { IAccountNotesSummary } from '@TriInterfaces/mac/mac.root.interfaces';
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";

@Injectable()
export class TriAccountHoldApi extends TriBaseApi<any> {

    constructor(private api: HttpService) {
        super();
    }

    getByMfaId(id: string): Promise<IAccountNotesSummary> {
        return this.api.get(`Nuvol9Api/MasterFileAccount/${id}/AccountHolds`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log("ACCOUNT HOLD GET DTO: ",dto);
            this.checkResponse(dto as DtoModel<any>);
            return dto.value;
        })
    }

    saveAccountHold(payload: any): Promise<any> {
        let input = {"jsonInput": JSON.stringify(payload)}
        return this.api.post(`Nuvol9Api/AccountHold`, input).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log("ACCOUNT HOLD SAVE DTO: ",dto);
            this.checkResponse(dto as DtoModel<any>);
            return dto.value;
        })
    }

    fixAccountHold(payload: any): Promise<any> {
        let input = { "jsonInput": JSON.stringify(payload) }
        return this.api.put(`Nuvol9Api/AccountHold`, input).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log("ACCOUNT HOLD FIX DTO: ",dto);
            this.checkResponse(dto as DtoModel<any>);
            return dto.value;
        })
    }

    deleteAccountHold(id: number): Promise<any> {
        return this.api.delete(`Nuvol9Api/AccountHold/${id}`).toPromise().then((data) => {
            const dto = data as DtoModel<any>;
            //console.log("ACCOUNT HOLD DELETE DTO: ",dto);
            this.checkResponse(dto as DtoModel<any>);
            return dto.value;
        })
    }

}