import { Injectable } from "@angular/core";
import { DtoModel } from "@TriInterfaces/root-dto.interfaces";
import { SecureNetContactModel } from "@TriInterfaces/mas/mas.secure-net.interfaces"
import { HttpService } from "../http.service";
import { TriBaseApi } from "../_base.api";
import { EmergencyContactModel } from "@TriInterfaces/mas/mas.emergency-contact.interfaces";

@Injectable()
export class TriSecureNetApi extends TriBaseApi<any>{

    constructor(private api: HttpService) {
        super();
    }

    getContactList(accountId:number): Promise<SecureNetContactModel[]> {
        return this.api.get(`Nuvol9Api/SecureNet/GetContactsList?id=${accountId}`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);

            //console.log("doop", JSON.parse(dto.value) as SecureNetContactModel[])
            return JSON.parse(dto.value) as SecureNetContactModel[];
        })
    }

    getContact(id:number, cId: string): Promise<any> {
        return this.api.get(`Nuvol9Api/SecureNet/GetContact?id=${id}&cId=${cId}`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);
            return dto.value;
        })
    }

    saveEmergencyContact(data: EmergencyContactModel): Promise<any> {
        return this.api.post(`Nuvol9Api/EmergencyContacts/Save`, data).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);
            return dto.value;
        })
    }


    deleteEmergencyContact(id: string): Promise<any> {
        return this.api.delete(`Nuvol9Api/EmergencyContacts/Del?id=${id}`).toPromise().then((data) => {

            const dto = data as DtoModel<any>;

            this.checkResponse(dto as DtoModel<any>);
            return dto.value;
        })
    }

}